<template>
  <div class="container">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="login_box"
    >
      <h3 class="login_titie">城市运行管理平台</h3>

      <el-form-item label="账号" prop="name">
        <el-input v-model="ruleForm.name"  placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="ruleForm.password"  placeholder="请输入密码" type="password"></el-input>
      </el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      ruleForm: {
        name: "admin",
        password: "6666666",
      },
      rules: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 9, message: "长度在 6 到 9 个字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.ruleForm.name == "admin"&&this.ruleForm.password == "6666666"){
          this.$message({
            message: "登陆成功",
            type: "success",
          });
          localStorage.setItem("token",'8888888')
          this.$router.push("/pageA");
          }else{
            this.$message({
            message: "账户或密码错误",
            type: "error",
          });
          }
        } else {
          this.$message({
            message: "账户或密码错误",
            type: "error",
          });
        }
      });
    },
  },
  mounted() {
    document.querySelector('body').setAttribute('style','background-image: url("+require("../../images/login_bg.jpg")+"background-repeat:no-repeat"')
  },
  beforeDestroy() {
    document.querySelector('body').removeAttribute('style')
  },
};
</script>

<style lang="less">
html{
  width: 100%;
  height: 100%;
  background-image: url("../../images/login_bg.jpg");
  background-size: 100% 100%;
}
.login_box {
  opacity: 0.9;
  width: 400px;
  margin: 250px auto;
  border: 1px solid #dcdfe6;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 0 30px #dcdfe6;
  background-color: #fff;
  background-color: rgb(12, 43, 79);
  .el-form-item__label {
    color: #bdcfda;
  }
  .el-form-item__content {
    color: #38505e;
  }
}
.login_titie {
  text-align: center;
  font-size: 20px;
  margin-bottom: 20px;
  color: #bdcfda
}
</style>
