import Vue from "vue";
import Vuex from "vuex";
import { getreside, getorganization,getinsurance,getpillar} from "../api";

const state = {
  title_name: "chart6",
  reside: [],
  organization:[],
  insurance:[],
  pillar:[],
  word_cloud_data:{},
};
const mutations = {
  SEND_TITLE(state, v) {
    state.title_name = v;
  },
  RESIDE(state, v) {
    // console.log(v);
    state.reside = v;
  },
  ORGANIZATION(state, v) {
    // console.log(v);
    state.organization = v;
  },
  INSURANCE(state, v) {
    // console.log(v);
    state.insurance = v;
  },
  PILLAR(state, v) {
    // console.log(v);
    state.pillar = v;
  },
  // WORDCLOUDDATD(state, v) {
  //   // console.log(v);
  //   state.word_cloud_data = v;
  // },
};
const actions = {
  async reside({ commit }) {
    let result = await getreside();
    commit("RESIDE", result);
  },
  async organization({ commit }) {
    let result = await getorganization();
    commit("ORGANIZATION", result);
  },
  async insurance({ commit }) {
    let result = await getinsurance();
    commit("INSURANCE", result);
  },
  async pillar({ commit }) {
    let result = await getpillar();
    commit("PILLAR", result);
  },
  // async word_cloud_data({ commit }) {
  //   let result = await getword_cloud_data();
  //   // console.log(result);
  //   commit("WORDCLOUDDATD", result);
  // },
};

Vue.use(Vuex);

export default new Vuex.Store({
  state,
  mutations,
  actions,
});
