<template>
  <div class="show_time">
    <router-link class="btnA" to="/pageA">页面A</router-link>
    <router-link class="btnB" to="/pageB">页面B</router-link>
    <router-link class="btnC" to="/pageC">页面C</router-link>
    <div class="time">
      <span>{{ date }}</span> <span>{{ time }}</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: "",
      date: "",
      week: [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ],
    };
  },
  methods: {
    updateTime() {
      var cd = new Date();
      this.time =
        this.zeroPadding(cd.getHours(), 2) +
        ":" +
        this.zeroPadding(cd.getMinutes(), 2) +
        ":" +
        this.zeroPadding(cd.getSeconds(), 2);
      this.date =
        this.zeroPadding(cd.getFullYear(), 4) +
        "年" +
        this.zeroPadding(cd.getMonth() + 1, 2) +
        "月" +
        this.zeroPadding(cd.getDate(), 2) +
        "日" +
        this.week[cd.getDay()];
    },
    zeroPadding(num, digit) {
      var zero = "";
      for (var i = 0; i < digit; i++) {
        zero += "0";
      }
      return (zero + num).slice(-digit);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
  },
  mounted() {
    setInterval(() => {
      this.updateTime();
    }, 1000);
    this.updateTime();
  },
};
</script>

<style scoped lang="less">
.show_time {
  position: relative;
  background: rgb(5, 9, 62);
  background-image: url("../../images/head_bg.png");
  background-size: 100% 100%;
  [class*="btn"] {
    width: 50px;
    height: 25px;
    background-color: rgb(233, 230, 230);
    position: absolute;
    top: 17px;
    border: 3px solid rgb(119, 115, 115);
    line-height: 20px;
  }
  .btnA {
    left: 10px;
  }
  .btnB {
    left: 80px;
  }
  .btnC {
    left: 150px;
  }
  .time {
    span {
      font-size: 0.35rem;
      color: #fff;
      text-align: center;
      line-height: 100px;
    }
  }
}
</style>
