<template>
  <div>
    <Header></Header>
    <div class="main_box">
      <div class="chart_title">part3</div>
      <div class="panel3">
        <div class="reside_box" v-for="reside in reside" :key="reside.index">
          <img src="../../images/fdf1840b58f39eeec55cfa57d12a44c.png" />
          <div class="reside_info_box">
            <div class="reside_title">{{ reside.name }}</div>
            <span class="reside_num">{{ reside.number }}</span
            ><span class="reside_unit">{{ reside.unit }}</span>
          </div>
        </div>
      </div>
      <baidu-map
        class="map"
        center="武汉"
        :mapStyle="mapStyle"
        :scroll-wheel-zoom="true"
      >
        <bm-marker :position="{ lng: 114.32, lat: 30.598 }">
          <bm-label
            content="武汉"
            :labelStyle="{ color: 'red', fontSize: '20px' }"
            :offset="{ width: 10, height: 25 }"
          />
        </bm-marker>
      </baidu-map>
      <!-- <div id="map"></div> -->
    </div>
  </div>
</template>

<script>
import Header from "../../components/Header";
import { mapState } from "vuex";

export default {
  name: "pageC",
  components: {
    Header,
  },
  data() {
    return {
      mapStyle: {
        styleJson: [
          {
            featureType: "all",
            elementType: "geometry",
            stylers: {
              hue: "#009fff",
              // saturation: 89,
            },
          },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      reside: (state) => state.reside,
    }),
  },
  mounted() {
    this.$store.dispatch("reside");
  },
};
</script>

<style scoped lang="less">
.main_box {
  width: 100%;
  height: 830px;
  padding: 0px 50px 0 50px;
  // background: #041c39;
  background: rgb(5, 9, 62);
  box-sizing: border-box;
  .chart_title {
    color: #fff;
    font-size: 20px;
    text-align: left;
    margin-left: 18px;
    height: 5%;
  }
  .panel3 {
    height: 45%;
    display: flex;
    flex-flow: wrap;
    img {
      margin-top: 20px;
    }
    .reside_title {
      color: #fff;
      height: 35%;
      padding-bottom: 10px;
    }
    .reside_num {
      color: #298ba8;
      font-size: 30px;
    }
    .reside_unit {
      color: #298ba8;
      margin-left: 10px;
    }
    [class*="reside_box"] {
      width: 50%;
      height: 50%;
      font-size: 20px;
      text-align: left;
      padding-left: 20px;
      position: relative;
      .reside_info_box {
        position: absolute;
        left: 100px;
        top: 0;
        margin-left: 10px;
        margin-top: 20px;
      }
    }
    .reside_box:nth-child(3) {
      width: 80%;
    }
  }
  .map {
    width: 100%;
    height: 50%;
  }
}
</style>
